
.rst {
  float: left;
  width: 2em;
  height: 2em;
  margin-bottom: 0.5em;
  border-radius: 3px;

  cursor: pointer;
}

.rst:hover { background-color: #b5dffd; }
.rst:hover { color: #444; }
.rst:hover .rst-dot {color: #444; }

.rst-left  { margin-right: 1em; }
.rst-right { margin-right: 0em; }

.rst-blue  { background-color: #01234C; color: #277EB9; }
.rst-green { background-color: #022E49; color: #7CA5C2; }

.rst-dot   { position: absolute; top: 0.3em; left: 0.5em;
             font-size: 0.5em; color: #CCC; }

.rst-label { position: absolute; top: 1.2em; left: 0.3em;
             font-size: 0.7em; font-weight: bold; }

