
body {
  background: url(gradient.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.App {
  font-family: 'Play', sans-serif;
}

#area-logo {
  position: fixed;
  top:      1em;
  left:     1em;

  padding:  0em 0.3em 0.0em 0.3em;

  background-color: #8F3C36;
  color: black;
  font-size: 1.1em;
  text-transform: uppercase;
}

#area-res-orbs {
  position: fixed;
  left:     15em;
  right:    5em;
  top:      1em;
}

#area-image-sensors {
  position: fixed;
  right:    1em;
  top:      0em;
  bottom:   6em;
  width:    8em;

  padding-top: 1.5em;

  text-align: center;
  background-color: #012946;
}

.image-sensor-header {
  color: #83AFC1;
  text-transform: uppercase;
}

#area-crumb-bar {
  position: fixed;
  top:      4em;
  right:    9em;
  left:     0em;
  height:   1.6em;

  padding-top: 0.2em;

  background-color: #7CC4ED;
}

#ports {
  float: right;
  padding-right: 1em;
}

#area-section-overview {
  position: fixed;
  top:      7em;
  bottom:   6em;
  left:     0.5em;
  width:    6em;

  padding-top: 0.2em;

  background-color: #041F37;
  text-align: center;
}

.section-logo img {
  width: 5em;
}

.section-name {
  text-transform: uppercase;
  color: white;
}

.logout {
  text-transform: uppercase;
  color: #075077;
}

#frame-inner {
  position: fixed;
  top:      7em;
  right:    10em;
  left:     7em;
}

#frame-inner-left {
  position: absolute;
  right:    6em;
  left:     0em;
}

#frame-inner-right {
  position: absolute;
  right:    0em;
  width:    6em;
}

#area-menu {
  height:   2em;

  padding:      1em;
  padding-left: 2em;

  background-color: #041F37;
}

#area-title {
  height:   3em;

  padding:  0em 0.5em 0em 0.5em;

  background-color: #041F37;
}

.title {
  padding-left:   0.5em;

  text-transform: uppercase;
  font-size:      1.5em;
  color:          white;

  border-top:     2px solid #318DCA;
  border-bottom:  2px solid #318DCA;
}

#area-rst {
  position: absolute;
  top:      0em;
  right:    0em;
  bottom:   0em;
  width:    5em;
}

#area-bottom {
  position: fixed;
  right:    0em;
  bottom:   0em;
  left:     0em;
  height:   6em;

  background-color: #041F37;
}

#section-menu {
  position: absolute;
  top:      1em;
  left:     1em;
  height:   2em;
}

.section-option {
  float: left;

  padding-right: 2em;

  text-align: center;
  color: white;
}

#section-menu img {
  width: 3em;
}

.option-name {
  font-size: 0.5em;
}

#copyright {
  position: absolute;
  right:    1em;
  bottom:   1em;

  color: #777;
  font-size: 0.6em;
}

#main-content {
  position: fixed;
  top:      14em;
  right:    16em;
  bottom:   6em;
  left:     7em;
  overflow: auto;

  padding:  0em 1em 0em 2em;

  background-color: #041F37;
  color: white;
}

#main-content h2 {
  margin-top: 0em;
  padding-left: 0.5em;

  text-decoration: uppercase;
  background-color: #7CC4ED;
  font-weight: normal;
}

#main-content p {
  padding-left: 1em;

  color: #DDD;
}

